.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 1s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateY(-30px); }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

*,
*:before,
*:after {
  box-sizing: border-box;
  outline: none;
  text-size-adjust: none; }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #9baacf;
  -webkit-font-smoothing: antialiased; }

body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
form,
fieldset,
legend,
button,
input,
textarea,
th,
td,
iframe {
  margin: 0;
  padding: 0; }

img,
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  display: block;
  margin: 0;
  padding: 0; }

fieldset,
img {
  border: 0; }

address,
caption,
cite,
dfn,
em,
th,
var,
i,
em {
  font-style: normal;
  font-weight: normal; }

ol,
ul {
  list-style: none; }

a {
  text-decoration: none;
  color: inherit; }
  a:hover {
    text-decoration: none; }

a,
label,
button,
input,
select {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

input,
select,
button {
  vertical-align: baseline;
  border-radius: 0;
  background-color: transparent; }

select {
  -webkit-appearance: none;
  -moz-appearance: none; }

button::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner,
input[type="file"] > input[type="button"]::-moz-focus-inner {
  border: none; }

input[type="checkbox"],
input[type="radio"] {
  vertical-align: middle; }

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  margin: 0; }

input:-webkit-autofill {
  background: transparent; }

input[type="search"],
input[type="tel"],
input[type="text"],
input {
  -webkit-appearance: none;
  -webkit-box-sizing: content-box;
  font-family: inherit;
  box-sizing: border-box; }

input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
  display: none; }

textarea {
  outline: none;
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  overflow: auto;
  resize: none;
  font: 100% tahoma, "\5b8b\4f53", arial; }

textarea::-webkit-input-placeholder,
input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #ccc; }

textarea:-moz-placeholder,
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #ccc;
  opacity: 1; }

textarea::-moz-placeholder,
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #ccc;
  opacity: 1; }

textarea:-ms-input-placeholder,
input:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #ccc; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-tag {
  position: relative;
  display: inline-flex;
  align-items: center;
  box-sizing: border-box;
  white-space: nowrap;
  color: #9baacf;
  padding: 4px 6px;
  font-size: 12px;
  line-height: 1;
  margin-right: 8px;
  margin-bottom: 8px;
  border-radius: 3px; }
  .tu-tag.is-hit {
    font-weight: bold; }
  .tu-tag--light {
    border: 1px solid #9baacf; }
  .tu-tag--dark {
    background: #bec8e4;
    color: #ffffff;
    border: 1px solid #bec8e4; }
  .tu-tag--plain {
    color: #bec8e4;
    border: 1px solid #bec8e4; }
    .tu-tag--plain .tu-tag__close {
      position: relative;
      color: var(--color);
      z-index: 100; }
    .tu-tag--plain::after {
      content: ' ';
      position: absolute;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      mix-blend-mode: soft-light;
      background-color: var(--color); }
  .tu-tag--shadow {
    padding: calc(4px + 1px) calc(6px + 1px);
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-tag--mini {
    font-size: 9px;
    padding: 3px 4px;
    border-radius: 1px;
    transform: scale(0.9); }
  .tu-tag--small {
    font-size: 10px;
    padding: 3px 5px;
    border-radius: 2px; }
  .tu-tag--large {
    font-size: 13px;
    padding: 6px 8px;
    border-radius: 4px; }
  .tu-tag .tu-tag__close {
    cursor: pointer;
    transition: all 0.3s ease;
    margin-left: 3px;
    transform: translateY(1px); }
    .tu-tag .tu-tag__close:hover {
      color: #6d5dfc; }
    .tu-tag .tu-tag__close::after {
      height: 12px; }
  .tu-tag:last-child {
    margin-right: 0;
    margin-bottom: 0; }
